export default {
	name: "UniTag",
	props: {
		type: {
			// 标签类型default、primary、success、warning、error、royal
			type: String,
			default: "default"
		},
		size: {
			// 标签大小 normal, small
			type: String,
			default: "normal"
		},
		// 标签内容
		text: {
			type: String,
			default: ""
		},
		disabled: {
			// 是否为禁用状态
			type: [String, Boolean],
			defalut: false
		},
		inverted: {
			// 是否为空心
			type: [String, Boolean],
			defalut: false
		},
		circle: {
			// 是否为圆角样式
			type: [String, Boolean],
			defalut: false
		},
		mark: {
			// 是否为标记样式
			type: [String, Boolean],
			defalut: false
		}
	},
	methods: {
		onClick() {
			if (this.disabled === true || this.disabled === "true") {
				return;
			}
			this.$emit("click");
		}
	}
};
